import React from "react";
import "bootstrap/dist/css/bootstrap.css";
import "./App.css";

export default function App() {
  return (
    <div>
      <h2>About me</h2>
      <p>
        My real name is Souporno Ghosh, but I maintain my online presence as The
        Anonymous Man (Not so anonymous now, am I?). I am also known as Ryamond
        Edmund in certain circles. I am an amateur video game designer. I am
        currently in college so I get very little time to make games, but I
        participate in Game Jams whenever possible.
      </p>
      <p>
        I have been wanting to be a video game developer since as long as I can
        remember. My dream first came true when my best friend introduced me to
        a course online on video game development. Truely, you can learn
        anything online these days. I made three games as part of a project for
        that course. And that is how I began. This was in 2018. I then didn't
        find the time to actually make a game of my own, unfortunately because
        college took a significant amount of my time. So, I had ideas for games,
        but nothing to show for it. That sucked.
      </p>
      <p>
        Next chapter of my life pertaining to video games came in 2019, when I
        heard of the Global Game Jam. That was my first Game Jam. And my first
        time making a complete game, that can be played, and that wasn't a board
        game. (I had made 1 ludo before that.) I learnt two things. First one
        was, I did something with 100% focus in 48 hours that I couldn't for
        months. Second one was, I learnt way more that I did in 2 years, than I
        did in those 2 days. After that, I participate in any Game Jam I can.
      </p>
      <p>
        All the games I have ever made can be found here. Play them, and tell me
        what you think of them.
      </p>
      <p>
        Email me at contacttheanonymousman@gmail.com, and I'll mail you back as
        soon as possible.
      </p>
    </div>
  );
}
